export default "'ലക്ഷക്കണക്കിന് വിളക്കുകൾ ഇങ്ങനെ കൂട്ടിയിട്ടിരിക്കുന്നതിനാൽ പല സ്ഥലത്തും ആളുകൾ വാരിക്കൊണ്ടു പോകുന്ന സ്ഥിതിയാണ്. അതുകൊണ്ട് നിലവിലെ സാമ്പത്തിക ബുദ്ധിമുട്ടിന്‍റെ സാഹചര്യത്തിൽ കണക്കെടുപ്പ് നടത്തി <b>ലേലം</b> ചെയ്താൽ ദേവസ്വം <b>ബോർഡിന് നല്ലൊരു വരുമാനം</b> ഇതിൽ നിന്നുണ്ടാകും' - തിരുവിതാംകൂർ ദേവസ്വം പ്രസിഡന്റ് എൻ.വാസു<br>  <br>- ന്യൂസ് 18<br><br><a href=\"https://malayalam.news18.com/news/kerala/temples-under-travancore-dewaswam-board-plans-to-sell-temple-properties-to-overcome-financial-crisis1-as-239699.html\">https://malayalam.news18.com/news/kerala/temples-under-travancore-dewaswam-board-plans-to-sell-temple-properties-to-overcome-financial-crisis1-as-239699.html</a>"
